<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="监测对象、设备编号、供应商过滤"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>

                <el-table-column
                    prop="devicecode"
                    label="设备编号"
                    min-width="3"
                ></el-table-column>

                <el-table-column
                    prop="siteshortname"
                    label="监测对象"
                    min-width="2"
                >
                </el-table-column>
                <el-table-column prop="supplier" label="供应商" min-width="2">
                </el-table-column>
                <el-table-column prop="isself" label="设备来源" min-width="1">
                    <template slot-scope="scope">
                        <el-tag
                            :type="
                                scope.row.isself == '1' ? 'success' : 'danger'
                            "
                            disable-transitions
                            >{{
                                scope.row.isself == 1 ? "自营" : "第三方"
                            }}</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column prop="status" label="状态" min-width="1">
                    <template slot-scope="scope">
                        <el-tag
                            effect="dark"
                            :type="
                                scope.row.status == '1' ? 'success' : 'danger'
                            "
                            disable-transitions
                            >{{
                                scope.row.status == 1 ? "在线" : "离线"
                            }}</el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    prop="checkintime"
                    label="最后上线时间"
                    min-width="2"
                >
                </el-table-column>
                <el-table-column
                    prop="checkouttime"
                    label="最后离线时间"
                    min-width="2"
                >
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-select
                                    v-model="editForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="监测对象" prop="SITEID">
                                <el-select
                                    v-model="editForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备来源" prop="isself">
                                <el-switch
                                    v-model="editForm.isself"
                                    active-text="自营"
                                    inactive-text="第三方"
                                    :active-value="1"
                                    :inactive-value="0"
                                >
                                </el-switch>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备编号" prop="devicecode">
                                <el-input
                                    v-model="editForm.devicecode"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="密码" prop="password">
                                <el-input
                                    v-model="editForm.password"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备IP" prop="deviceip">
                                <el-input
                                    v-model="editForm.deviceip"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备端口" prop="deviceport">
                                <el-input
                                    v-model="editForm.deviceport"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="服务供应商" prop="supplier">
                                <el-select
                                    placeholder="请选择"
                                    v-model="editForm.supplier"
                                >
                                    <el-option
                                        v-for="(key, value) in mprovider"
                                        :key="key"
                                        :label="value"
                                        :value="value"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <!-- <el-col :span="12">
                            <el-form-item label="摄像头编号" prop="cameracode">
                                <el-input
                                    v-model="editForm.cameracode"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col> -->
                        <el-col :span="12">
                            <el-form-item
                                label-width="200px"
                                label="扬尘PM10数值控制线"
                                prop="fogkickline"
                            >
                                <el-input
                                    type="number"
                                    v-model="editForm.fogkickline"
                                    auto-complete="off"
                                    maxlength="8"
                                    placeholder="建议设置在900以上"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-select
                                    v-model="addForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="监测对象" prop="SITEID">
                                <el-select
                                    v-model="addForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备来源" prop="isself">
                                <el-switch
                                    v-model="addForm.isself"
                                    active-text="自营"
                                    inactive-text="第三方"
                                    :active-value="1"
                                    :inactive-value="0"
                                >
                                </el-switch>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备编号" prop="devicecode">
                                <el-input
                                    v-model="addForm.devicecode"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="密码" prop="password">
                                <el-input
                                    v-model="addForm.password"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备IP" prop="deviceip">
                                <el-input
                                    v-model="addForm.deviceip"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="设备端口" prop="deviceport">
                                <el-input
                                    v-model="addForm.deviceport"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="服务供应商" prop="supplier">
                                <el-select
                                    placeholder="请选择"
                                    v-model="addForm.supplier"
                                >
                                    <el-option
                                        v-for="(key, value) in mprovider"
                                        :key="key"
                                        :label="value"
                                        :value="value"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <!-- <el-col :span="12">
                            <el-form-item label="摄像头编号" prop="cameracode">
                                <el-input
                                    v-model="addForm.cameracode"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col> -->
                        <el-col :span="12">
                            <el-form-item
                                label-width="200px"
                                label="扬尘PM10数值控制线"
                                prop="fogkickline"
                            >
                                <el-input
                                    type="number"
                                    v-model="addForm.fogkickline"
                                    auto-complete="off"
                                    maxlength="8"
                                    placeholder="建议设置在900以上"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getDevicepagelist,
    getDeviceGroups,
    removeDevice,
    editDevice,
    addDevice,
    getNoDeviceList,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import { mprovider } from "../../assets/dic/dic";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                devicecode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                supplier: [
                    {
                        required: true,
                        message: "请选择服务供应商",
                        trigger: "blur",
                    },
                ],
                password: [
                    {
                        required: true,
                        message: "请输入密码",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                devicecode: [
                    {
                        required: true,
                        message: "请填写设备编号",
                        trigger: "blur",
                    },
                ],
                supplier: [
                    {
                        required: true,
                        message: "请选择服务供应商",
                        trigger: "blur",
                    },
                ],
                password: [
                    {
                        required: true,
                        message: "请输入密码",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectGroupId: 0,
            // sblx: [],
            mprovider: mprovider,
            selectSites: [],
            groupAllSites: [],
        };
    },
    methods: {
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getDevicepagelist(paras).then((res) => {
                if (res.success) {
                    this.builds = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getDeviceGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.DEVICEID };
                    removeDevice(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList(this.page);
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }

            this.groupChange(row.GROUPID, row.SITEID);
            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.selectSites = [];
            this.addFormVisible = true;
            this.addForm = {};
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    editDevice(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addDevice(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
        groupChange(val, SITEID) {
            // this.getGroupSites(val, true);
            this.getNoDeviceList(val, SITEID);
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            // this.selectSites = this.groupSites;
            // this.selectSites.unshift({ SITEID: 0, siteshortname: "无" });
        },
        getNoDeviceList(GROUPID, SITEID) {
            let paras = {
                GROUPID: GROUPID,
            };
            this.groupAllSites = [];
            getNoDeviceList(paras).then((res) => {
                if (res.success) {
                    this.groupAllSites = res.data;

                    let list = this.groupAllSites.filter((item) => {
                        return (
                            item.hasdev == 0 ||
                            (SITEID && SITEID == item.SITEID)
                        );
                    });
                    list.unshift({ SITEID: 0, siteshortname: "无" });
                    this.selectSites = list;
                }
            });
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        // this.sblx = this.$store.state.options.sblx;
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
</style>


